.container {
  max-width: rem(1520px);
  width: 100%;
  margin: 0 auto;
}

.btn {
  width: 100%;
  height: rem(50px);
  color: $orange_color;
  font-family: $Sansus_Webissimo;
  font-size: rem(20px);
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: rem(0.8px);
  text-transform: uppercase;
  background-color: #ffffff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: background-color .3s, color .4s;
  &:hover {
    background-color: $orange_color;
    color: #fff;
  }
}

//FRANCE TEXT
.france_text {
  text-indent: rem(20px);
  font-family: $Roboto;
  font-size: rem(20px);
  font-weight: 300;
  line-height: 1.7;
  b {
    font-family: $Sansus_Webissimo;
    font-weight: 400;
    letter-spacing: rem(0.8px);
    text-transform: uppercase;
  }
}

.main_title {
  color: #fefeff;
  font-family: $Sansus_Webissimo;
  font-size: rem(64px);
  font-weight: 400;
  letter-spacing: rem(2.56px);
}

.line {
  position: relative;
  width: rem(279px);
  height: rem(1px);
  background-color: $orange_color;
  span {
    position: absolute;
    left: rem(-5px);
    top: rem(-3px);
    width: rem(53px);
    height: rem(7px);
    border-radius: rem(4px);
    background-color: $orange_color;
  }
  //&:nth-child(2n+2) {
  //  span {
  //    right: rem(-5px);
  //  }
  //}
}


