.france_day {
  position: relative;
  width: 100%;
  overflow: hidden;
  height: rem(602px);
  &::before {
    @include before_fig(100%, 100%, rgba(7, 25, 71, 0.75));
    z-index: 2;
    background: linear-gradient(0deg, rgba(7, 25, 71, .75), transparent)
  }
  .parallax {
    position: absolute;
  }
  .france_day_title {
    width: 100%;
    height: 100%;
    position: relative;
    bottom: rem(80px);
    z-index: 3;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: flex-end;

    h2 {
      color: $orange_color;
      font-family: $Sansus_Webissimo;
      font-size: rem(64px);
      font-weight: 400;
      letter-spacing: rem(2.56px);
    }
    p {
      color: #ffffff;
      font-family: $Roboto;
      font-size: rem(33px);
      font-weight: 300;
      letter-spacing: rem(1.32px);
    }

  }

}

.france_day_content {
  margin-top: rem(75px);
  width: 100%;
  position: relative;
  .container {
    width: 100%;
    p {
      text-align: center;
      width: 70%;
      margin: rem(50px) auto;
      color: #071947;
    }
    .france_day_img {
      margin-top: rem(70px);
      position: relative;
      width: 100%;
      display: flex;
      justify-content: space-between;
      margin-bottom: rem(75px);
      .day_img_elem {
        max-width: rem(280px);
        width: 100%;
        height: rem(280px);
        position: relative;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }
}


